export function hitTestRectangle(r1: any, r2: any) {
  //å®šä¹‰éœ€è¦è®¡ç®—çš„å˜é‡
  let hit;

  //æ ‡è®°æ˜¯å¦å‘ç”Ÿç¢°æ’
  hit = false;

  //æ‰¾åˆ°æ¯ä¸ªç²¾çµçš„ä¸­å¿ƒç‚¹
  r1.centerX = r1.x + r1.width / 2;
  r1.centerY = r1.y + r1.height / 2;
  r2.centerX = r2.x + r2.width / 2;
  r2.centerY = r2.y + r2.height / 2;

  //æ‰¾å‡ºæ¯ä¸ªç²¾çµçš„ä¸€åŠå®½åº¦å’Œä¸€åŠé«˜åº¦
  r1.halfWidth = r1.width / 2;
  r1.halfHeight = r1.height / 2;
  r2.halfWidth = r2.width / 2;
  r2.halfHeight = r2.height / 2;

  //è®¡ç®—ç²¾çµä¹‹é—´çš„è·ç¦»å‘é‡
  const vx = r1.centerX - r2.centerX;
  const vy = r1.centerY - r2.centerY;

  //Figure out the combined half-widths and half-heights
  const combinedHalfWidths = r1.halfWidth + r2.halfWidth;
  const combinedHalfHeights = r1.halfHeight + r2.halfHeight;

  //æ£€æŸ¥xè½´ä¸Šæ˜¯å¦æœ‰ç¢°æ’
  if (Math.abs(vx) < combinedHalfWidths) {
    //æ£€æŸ¥yè½´ä¸Šæ˜¯å¦æœ‰ç¢°æ’
    if (Math.abs(vy) < combinedHalfHeights) {
      //ç¢°æ’
      hit = true;
    } else {
      //æ²¡æœ‰ç¢°æ’
      hit = false;
    }
  } else {
    //xè½´æ— ç¢°æ’
    hit = false;
  }

  return hit;
}
