interface key {
  value: string;
  isDown: boolean;
  isUp: boolean;
  press: (() => void) | undefined;
  release: (() => void) | undefined;
  downHandler: ((event: KeyboardEvent) => void) | undefined;
  upHandler: ((event: KeyboardEvent) => void) | undefined;
  unsubscribe: ((event: KeyboardEvent) => void) | undefined;
}

const moveSpeed = 5; //é£æœºç§»åŠ¨é€Ÿåº¦

//é”®ç›˜äº‹ä»¶
function keyboard(value: string) {
  const key: key = {
    value: value,
    isDown: false,
    isUp: true,
    press: undefined,
    release: undefined,
    downHandler: undefined,
    upHandler: undefined,
    unsubscribe: undefined,
  };

  //æŒ‰é”®æŒ‰ä¸‹æ—¶è§¦å‘
  key.downHandler = (event) => {
    if (event.key === key.value) {
      if (key.isUp && key.press) key.press();
      key.isDown = true;
      key.isUp = false;
      event.preventDefault();
    }
  };

  //æŒ‰é”®æŠ¬èµ·æ—¶è§¦å‘
  key.upHandler = (event) => {
    if (event.key === key.value) {
      if (key.isDown && key.release) key.release();
      key.isDown = false;
      key.isUp = true;
      event.preventDefault();
    }
  };

  //æ·»åŠ äº‹ä»¶ç›‘å¬å™¨
  const downListener = key.downHandler.bind(key);
  const upListener = key.upHandler.bind(key);

  window.addEventListener("keydown", downListener, false);
  window.addEventListener("keyup", upListener, false);

  //å¸è½½äº‹ä»¶ç›‘å¬
  key.unsubscribe = () => {
    window.removeEventListener("keydown", downListener);
    window.removeEventListener("keyup", upListener);
  };

  return key;
}

//é¼ æ ‡æ§åˆ¶é£æœº(cxk)çš„ç§»åŠ¨
function onDragMove(event: any) {
  const cur = event.currentTarget;
  const { x, y } = event.data.global;
  cur.x = x - 50;
  cur.y = y - 75;
}

export default function planeMove(plane: {
  on: (arg0: string, arg1: (event: any) => void) => void;
  vx: number;
  vy: number;
}) {
  plane.on("pointermove", onDragMove);
  //æ•æ‰é”®ç›˜ä¸Šçš„ç®­å¤´é”®
  const left = keyboard("ArrowLeft"),
    up = keyboard("ArrowUp"),
    right = keyboard("ArrowRight"),
    down = keyboard("ArrowDown");

  //å·¦é”®æŒ‰ä¸‹
  left.press = () => {
    plane.vx = -moveSpeed;
    plane.vy = 0;
  };
  //å·¦é”®é‡Šæ”¾
  left.release = () => {
    //å¦‚æœå·¦ç®­å¤´å·²æ¾å¼€ï¼Œå³ç®­å¤´æœªæŒ‰ä¸‹ï¼Œä¸”çŒ«æœªå‚ç›´ç§»åŠ¨ï¼šåœæ­¢çŒ«
    if (!right.isDown && plane.vy === 0) {
      plane.vx = 0;
    }
  };

  //ä¸Šç®­å¤´é”®æŒ‰ä¸‹
  up.press = () => {
    plane.vy = -moveSpeed;
    plane.vx = 0;
  };
  //ä¸Šç®­å¤´é”®æŠ¬èµ·
  up.release = () => {
    if (!down.isDown && plane.vx === 0) {
      plane.vy = 0;
    }
  };

  //å³ç®­å¤´æŒ‰ä¸‹
  right.press = () => {
    plane.vx = moveSpeed;
    plane.vy = 0;
  };
  //å³ç®­å¤´æŠ¬èµ·
  right.release = () => {
    if (!left.isDown && plane.vy === 0) {
      plane.vx = 0;
    }
  };

  //ä¸‹ç®­å¤´æŒ‰ä¸‹
  down.press = () => {
    plane.vy = moveSpeed;
    plane.vx = 0;
  };
  //ä¸‹ç®­å¤´æŠ¬èµ·
  down.release = () => {
    if (!up.isDown && plane.vx === 0) {
      plane.vy = 0;
    }
  };
}
