const move = {
  mounted: function (el: {
    clientWidth: number;
    clientHeight: number;
    style: { left: string; top: string };
  }) {
    const offsetW = el.clientWidth / 2; //å…ƒç´ å®½åº¦çš„ä¸€åŠ
    const offsetH = el.clientHeight / 2; //å…ƒç´ é«˜åº¦çš„ä¸€åŠ
    const clientWidth = document.body.clientWidth; //æµè§ˆå™¨çª—å£å®½åº¦
    const clientHeight = document.body.clientHeight; //æµè§ˆå™¨çª—å£é«˜åº¦
    document.onmousemove = function (ev) {
      let l = ev.clientX; //é¼ æ ‡ä½ç½®è·ç¦»çª—å£å·¦ä¾§åç§»
      let t = ev.clientY; //é¼ æ ‡ä½ç½®è·ç¦»çª—å£é¡¶éƒ¨åç§»
      //å…ƒç´ ç¢°åˆ°å·¦ä¾§çª—å£
      if (l < offsetW) {
        l = offsetW;
      }
      //å…ƒç´ ç¢°åˆ°å³ä¾§çª—å£
      if (l > clientWidth - offsetW) {
        l = clientWidth - offsetW;
      }
      //å…ƒç´ ç¢°åˆ°ä¸Šä¾§çª—å£
      if (t < offsetH) {
        t = offsetH;
      }
      //å…ƒç´ ç¢°åˆ°åº•éƒ¨çª—å£
      if (t > clientHeight - offsetH) {
        t = clientHeight - offsetH;
      }
      el.style.left = l - offsetW + "px";
      el.style.top = t - offsetH + "px";
    };
  },
};

export { move };
